import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, Events } from 'ionic-angular';
import { StorageProvider } from '../../providers/storage';
import { DragulaService } from 'ng2-dragula';
import { Subscription } from 'rxjs';
@IonicPage()
@Component({
  selector: 'page-edit-application',
  templateUrl: 'edit-application.html',
})
export class EditApplicationPage {
  public appAuthority=[];
  public allApplication=[];
  public addApplication=[];
  subs = new Subscription();
  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public storage:StorageProvider,
              public events:Events,
              public dragula:DragulaService,
              
              ) {

  }
  ngOnDestroy() {
    // destroy all the subscriptions at once
    this.subs.unsubscribe();
  }
  ionViewWillEnter(){
    this.appAuthority=this.storage.get(StorageProvider.HOME_APPAUTHORITY);
    console.log(this.appAuthority);
    this.allApplication=this.storage.get(StorageProvider.ALLAPPLICATION);
    console.log(this.allApplication);
    for(let i=0;i<this.allApplication.length;i++){
      // let obj={};
      var flag=0;
      for(let j = 0 ;j<this.appAuthority.length;j++){
        if(this.allApplication[i].code!=this.appAuthority[j].code){
          flag=flag+1;
          if(flag==this.appAuthority.length){
            this.addApplication.push(this.allApplication[i]);
          }
        }
      }
    }
  }
  keepApplication(){
    this.storage.set(StorageProvider.HOME_APPAUTHORITY,this.appAuthority);
    this.navCtrl.pop(); 
  }
  addHomeApplication(item,index){
    console.log(index);
    this.addApplication.splice(index,1);
    this.appAuthority.push(item);
  }
  deleteApplication(item,index){
    this.appAuthority.splice(index,1);
    this.addApplication.push(item);
  }
}



